<template>
  <div class="container">
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="搜索">
              <el-input v-model="searchs.keyword_search_value" placeholder="姓名/手机号"></el-input>
            </el-form-item>
            <el-form-item label="注册IP">
              <el-input v-model="searchs.reg_ip" placeholder="IP"></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="渠道">
              <el-select v-model="searchs.channel" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.channel" :key="name" :label="value" :value="name">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="用户等级">
              <el-select v-model="searchs.level" clearable placeholder="请选择">
                <el-option v-for="(value, key) in config.level" :key="key" :label="value.name" :value="value.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="自定义渠道">
              <el-select v-model="searchs.channel_id" clearable placeholder="请选择">
                <el-option v-for="(value, key) in placelist" :key="key" :label="value.name" :value="value.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="认证">
              <el-select v-model="searchs.is_auth" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.is_auth" :key="name" :label="value" :value="name">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="注册时间">
              <el-date-picker
                class="data-input"
                v-model="addtime"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
              />
            </el-form-item>
            <el-form-item label="只看在线">
              <el-checkbox v-model="searchs.is_online" placeholder="姓名/手机号"></el-checkbox>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
              <el-button type="success" @click="createUser">新增</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            <div
              style="height: 45px;line-height: 45px;text-align: left;display: inline-block; color: #F00;"
              class="infos"
            >
              在线人数: {{ online_user_num }} 人, {{ channel_info }}
            </div>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="uid" label="编号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="credit" label="会员积分" align="center" />
      <el-table-column prop="exchange_credit" label="兑换积分" align="center" />
      <el-table-column prop="prize_num" label="抽奖次数" align="center" />
      <el-table-column prop="remark" label="[管理员]备注" align="center" />
      <el-table-column prop="remark" label="[管理员]备注" align="center" />
      <el-table-column prop="mobile" label="手机号" width="150" align="center" />
      <el-table-column align="center" label="在线状态">
        <template slot-scope="scope">
          <el-tag :type="scope.row.online_status === '在线' ? 'success' : 'info'" size="small" effect="dark">
            {{ scope.row.online_status }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="channel" label="注册渠道" align="center" width="150px" />
      <el-table-column prop="reg_ip" label="注册IP" align="center" width="150px">
        <template slot-scope="scope">
          <el-link :underline="false" type="primary" @click="toIp(scope.row.reg_ip)">{{ scope.row.reg_ip }} </el-link>
        </template>
      </el-table-column>
      <el-table-column prop="reg_addr" label="注册地址" align="center" width="150px" />
      <el-table-column prop="last_login_ip" label="最近登录IP" align="center" width="150px">
        <template slot-scope="scope">
          <el-link :underline="false" type="primary" @click="toIp(scope.row.last_login_ip)">
            {{ scope.row.last_login_ip }}
          </el-link>
        </template>
      </el-table-column>
      <el-table-column prop="last_login_addr" label="最近登录地址" align="center" width="150px" />
      <el-table-column prop="money" label="账户余额" align="center" />
      <el-table-column prop="user_level" label="用户等级" align="center" />
      <el-table-column prop="clear_text" label="用户密码" align="center" />
      <el-table-column prop="is_auth_name" align="center" label="是否认证" />
      <el-table-column prop="status_name" align="center" label="用户状态" />
      <el-table-column prop="top_mobile" label="推荐人手机号" align="center" width="130px" />
      <el-table-column align="center" label="时间" width="240">
        <template slot-scope="scope">
          <div>注册时间：{{ scope.row.addtime_date }}</div>
          <div>最近操作：{{ scope.row.uptime_date }}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width="330" fixed="right">
        <template slot-scope="scope">
          <el-button size="small" @click="editItem(scope.row.uid)" type="primary">编辑</el-button>
          <el-button size="small" @click="openCtrlMoney(scope.row.uid)" type="warning">增减余额|积分</el-button>
          <el-button size="small" type="danger" @click="() => setFreeze(scope.row.uid)">冻结</el-button>
          <el-button size="small" type="danger" @click="removeFreeze(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="增减数值" :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="moneyCtrl" ref="moneyCtrl" :rules="rules" @submit.native.prevent="ctrlMoney" label-width="80px">
        <el-form-item label="操作值">
          <el-input v-model="moneyCtrl.num" placeholder="请输入值" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="标题">
          <el-input v-model="moneyCtrl.title" placeholder="请输入标题" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="moneyCtrl.stype" placeholder="请选择变更类型">
            <el-option label="金额" value="money"></el-option>
            <el-option label="会员积分" value="credit"></el-option>
            <el-option label="兑换积分" value="exchange_credit"></el-option>
            <el-option label="抽奖次数" value="prize_num"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="增减">
          <el-select v-model="moneyCtrl.type" placeholder="请选择增加或减少">
            <el-option label="增加" value="add"></el-option>
            <el-option label="减少" value="sub"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="text-align: right;" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" native-type="submit">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import dayjs from 'dayjs'
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      count: 0,
      page_curren: 1,
      searchs: {
        keyword_search_value: '',
        status: '',
        is_auth: '',
        reg_ip: '',
        begin_addtime: '',
        end_addtime: '',
        channel: '',
        channel_id: '',
        level: '',
      },
      placelist: {},
      config: {
        status: {},
        is_auth: {},
        channel: {},
      },
      moneyCtrl: {
        id: '',
        num: '',
        type: '',
        stype: '',
        title: '',
      },
      prizeCtrl: {
        id: '',
        num: '',
        type: '',
        remark: '',
      },
      anwserCtrl: {
        id: '',
        num: '',
        type: '',
        remark: '',
      },
      rules: {
        money: [{ required: true, message: '请输入金额' }],
        type: [{ required: true, message: '请选择增加或减少' }],
      },
      channel_info: '',
      tableData: [],
      loading: false,
      online_user_num: 0,
      user_num: 0,
      dialogFormVisible: false,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      dialogLoading: false,
      addtime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    toIp(ip) {
      this.searchs.reg_ip = ip
      this.FetchList()
    },
    openCtrlMoney(uid) {
      this.moneyCtrl.id = uid
      this.dialogFormVisible = true
    },
    openCtrlPrize(uid) {
      this.prizeCtrl.id = uid
      this.dialogFormVisible1 = true
    },
    openCtrlAnwser(uid) {
      this.anwserCtrl.id = uid
      this.dialogFormVisible2 = true
    },
    ctrlMoney() {
      this.$refs.moneyCtrl.validate(valid => {
        if (valid) {
          this.dialogLoading = true
          Fetch('/user/user/setMoney', {
            ...this.moneyCtrl,
          })
            .then(() => {
              this.dialogLoading = false
              this.dialogFormVisible = false
              Message.success('操作成功')
              this.FetchList()
            })
            .catch(res => {
              this.dialogLoading = false
              Message.error(res.msg)
            })
        }
      })
    },
    ctrlPrize() {
      this.$refs.prizeCtrl.validate(valid => {
        if (valid) {
          this.dialogLoading = true
          Fetch('/user/user/setPrize', {
            ...this.prizeCtrl,
          })
            .then(() => {
              this.dialogLoading = false
              this.dialogFormVisible1 = false
              Message.success('操作成功')
              this.FetchList()
            })
            .catch(res => {
              this.dialogLoading = false
              Message.error(res.msg)
            })
        }
      })
    },
    anwserPrize() {
      this.$refs.anwserCtrl.validate(valid => {
        if (valid) {
          this.dialogLoading = true
          Fetch('/user/user/setAnwser', {
            ...this.anwserCtrl,
          })
            .then(() => {
              this.dialogLoading = false
              this.dialogFormVisible2 = false
              Message.success('操作成功')
              this.FetchList()
            })
            .catch(res => {
              this.dialogLoading = false
              Message.error(res.msg)
            })
        }
      })
    },
    createUser() {
      this.$router.push('/member/list/create')
    },
    FetchList() {
      this.loading = true
      Fetch('/user/user/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.config = r.data.config
          this.placelist = r.data.placelist
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.user_num = r.data.user_num
          this.online_user_num = r.data.online_user_num
          this.channel_info = r.data.channel_info
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(uid) {
      this.$router.push('/member/list/' + uid)
    },
    setStatus(uid) {
      Fetch('/user/user/freeze', { uid }).then(this.FetchList)
    },
    setFreeze(uid) {
      Fetch('/user/user/freeze', { uid }).then(this.FetchList)
    },
    removeFreeze(uid) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          Fetch('/user/user/remove', { uid }).then(() => {
            this.FetchList()
            this.$message({
              type: 'success',
              message: '删除成功!',
            })
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
  },
}
</script>
<style lang="less" scoped>
.searchs {
  background-color: #f2f2f2;
  border-left: 5px solid #1aa094;
  padding: 14px 0 14px 15px;
  color: #606266;
  font-size: 16px;

  .form-search {
    /deep/ .el-input__inner {
      width: 150px;
    }

    .data-input {
      width: 300px;
    }
  }
}

.pagination {
  text-align: right;
  margin-top: 20px;
}

.list {
  width: 100%;
  margin-top: 20px;
  text-align: center;

  /deep/ .el-table__header-wrapper {
    th {
      background-color: #f2f2f2;
    }
  }
}

.container {
  /deep/ .el-select {
    width: 100%;
  }
}
</style>
